  font-size: 20px;
  border: 0;
}
#signin {
  background: transparent;
  font-family: "Now";
  font-weight: 400;
  color: white;
  cursor: pointer;
  margin: 0% 8%;
}
#signupbutton {
  font-family: "Now";
  font-weight: 500;
  background: linear-gradient(
    105.64deg,
    #ffffff -20.33%,
    rgba(221, 186, 255, 0.76) 113.08%
  );
  border: 1px solid #162378;
  box-shadow: 0px 0px 35px rgba(253, 225, 255, 0.52);
  border-radius: 42px;
  padding: 3% 7%;
  padding-top: 3.5%;
  cursor: pointer;
}
.text h1 {
  font-size: 3rem;
  font-family: "Now";
  font-weight: 900;
  font-style: normal;
  display: inline-block;
  margin: auto;
}
.head h1 {
  margin: 0px;
  position: absolute;
  left: 40%;
  margin-top: 5%;
}
.head {
  height: 10vw;
  position: relative;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-top: 1.5%;
  width: 100%;
}
.head img {
  height: 130%;
  width: auto;
}

#slogan_text {
  margin-top: 13%;
}

.btn-div {
  margin-top: 20%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

#outline {
  position: absolute;
  height: 40%;
  width: 20%;
  left: 43%;
  mix-blend-mode: overlay;
  margin-top: -15px;
}
@keyframes girl {
  from {
    transform: translateY(0) rotate(0deg);
  }
  to {
    transform: translateY(2%) rotate(1deg);
  }
}
@keyframes square {
  from {
    transform: translateY(0) translateX(0);
  }
  to {
    transform: translateY(28%) translateX(26%) rotate(3deg);
  }
}
@keyframes circle {
  from {
    transform: translateY(0) translateX(0);
  }
  to {
    transform: translateY(20%) translateX(16%);
  }
}
@keyframes cross {
  from {
    transform: translateY(0) translateX(0);
  }
  to {
    transform: translateY(22%) translateX(-11%) rotate(-5deg);
  }
}

#g4 {
  animation-name: square;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
  animation-direction: alternate;
}
#g1 {
  animation-name: circle;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-direction: alternate;
}
#g3,
#g2 {
  animation-name: cross;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-direction: alternate;
}
@keyframes extender {
  from {
    transform: translateX(0) scaleX(1);
  }
  to {
    transform: translateX(15px) scaleX(1.1);
  }
}
@keyframes extenderpart2 {
  from {
    transform: translateX(0) scaleX(1);
  }
  to {
    transform: translateX(-35px) scaleX(1.2);
  }
}
@keyframes extenderpart3 {
  from {
    transform: translateY(0) scaleY(1) rotate(90deg);
  }
  to {
    transform: translateY(-40px) scaleY(1.3) rotate(90deg);
  }
}
#l3 {
  animation-name: extenderpart3;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-direction: alternate;
}
#l1 {
  animation-name: extender;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-direction: alternate;
}
#l2 {
  animation-name: extenderpart2;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-direction: alternate;
}

/* landing scroll */

html {
  overflow: hidden;
}
@keyframes scale {
  0% {
    transform: scale(1) translateY(0);
  }
  100% {
    transform: scale(1.1) translateY(3px);
  }
}
.rotate_footer_arrow {
  animation: rotate_footer_arrow 600ms ease-in-out 1 alternate forwards;
}
.rotate_footer_arrow_2 {
  animation: rotate_footer_arrow_2 550ms ease-in-out 1 forwards;
}
.scale {
  animation: scale 1000ms ease-in-out infinite alternate;
}

@keyframes rotate_footer_arrow {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}
@keyframes rotate_footer_arrow_2 {
  from {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(0deg);
  }
}
.arrow {
  position: absolute;
  width: 7%;
  height: auto;
  bottom: 4%;
  left: 46.5%;
}
.arrow:hover {
  cursor: pointer;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.footer {
  background: #002091;
  width: 100%;
  height: 275px;
  display: flex;
  color: white;
  justify-content: space-between;
}
.display-none {
  display: none;
}
.footer-content {
  display: flex;
  width: 50%;
  justify-content: space-between;
  margin: 50px 75px;
}
.footer h2 {
  font-family: "Now Alt";
  font-weight: 500;
  font-style: normal;
  color: #b3b2ff;
  margin-bottom: 30px;
}
a {
  color: white;
  text-decoration: none;
  font-family: "Now Alt";
  font-weight: 400;
  font-style: normal;
  margin-bottom: 10px;
  cursor: pointer;
}
.links,
.tc,
.help {
  display: flex;
  flex-direction: column;
}
.follow {
    margin: 50px 75px;
  }
  .follow img {
    margin-right: 15px;
  }
.main {
  filter: brightness(100%);
  transition: filter 600ms ease-in-out;
}
.main-opacity {
  filter: brightness(50%);
  transition: filter 600ms ease-in-out;
}

/* dom manipulation */
.display_to_none {
  animation: to_opacity_none 0.3s ease-in-out 1;
  display: none;
}

@keyframes to_opacity_none {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.display_to_block {
  display: block;
  animation: to_display_block 0.3s ease-in-out 1;
}

@keyframes to_display_block {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

